/* Add Attachment Modal Styles */
.modal {
  z-index: 1055;
}

.modal.show {
  display: block !important;
}

.modal-backdrop.show {
  opacity: 0.5;
}

.modal-dialog {
  max-width: 600px;
}

.modal-header {
    border-bottom: 1px solid rgba(23, 162, 184, 0.2);
    padding: 1rem 1.5rem;
    display: flex;
    align-items: flex-start !important;
}

.modal-title {
  font-size: 1.25rem;
  font-weight: 600;
  margin: 0;
}.modal-body {
  padding: 1.5rem;
  max-height: 70vh;
  overflow-y: auto;
}

.modal-footer {
  border-top: 1px solid #dee2e6;
  padding: 1rem 1.5rem;
  background-color: #f8f9fa;
}

/* File Drop Zone */
.file-drop-zone {
  border: 2px dashed #ced4da;
  border-radius: 0.5rem;
  padding: 2rem;
  text-align: center;
  cursor: pointer;
  transition: all 0.3s ease;
  background-color: #f8f9fa;
  min-height: 200px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.file-drop-zone:hover {
  border-color: #17a2b8;
  background-color: #e6f7ff;
}

.file-drop-zone.has-file {
  border-color: #28a745;
  background-color: #e8f5e8;
}

.file-drop-content {
  width: 100%;
}

.file-drop-content h5 {
  margin-bottom: 0.5rem;
}

.file-drop-content p {
  margin-bottom: 1rem;
}

/* Selected File Display */
.selected-file-display {
  width: 100%;
  padding: 1rem;
  background-color: #fff;
  border-radius: 0.375rem;
  border: 1px solid #dee2e6;
}

/* Form styling */
.form-label {
  font-weight: 500;
  color: #495057;
  margin-bottom: 0.5rem;
}

.form-control, .form-select {
  border: 1px solid #ced4da;
  border-radius: 0.375rem;
  padding: 0.5rem 0.75rem;
  font-size: 0.875rem;
  transition: border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out;
}

.form-control:focus, .form-select:focus {
  border-color: #17a2b8;
  box-shadow: 0 0 0 0.2rem rgba(23, 162, 184, 0.25);
  outline: 0;
}

.form-control.is-invalid, .form-select.is-invalid {
  border-color: #dc3545;
}

.form-control.is-invalid:focus, .form-select.is-invalid:focus {
  border-color: #dc3545;
  box-shadow: 0 0 0 0.2rem rgba(220, 53, 69, 0.25);
}

.invalid-feedback {
  display: block;
  width: 100%;
  margin-top: 0.25rem;
  font-size: 0.875rem;
  color: #dc3545;
}

/* Textarea specific styling */
textarea.form-control {
  resize: vertical;
  min-height: 80px;
}

/* Button styling */
.btn {
  border-radius: 0.375rem;
  font-weight: 500;
  padding: 0.5rem 1rem;
  transition: all 0.15s ease-in-out;
}

.btn-info {
  background-color: #17a2b8;
  border-color: #17a2b8;
  color: #fff;
}

.btn-info:hover:not(:disabled) {
  background-color: #138496;
  border-color: #117a8b;
}

.btn-info:disabled {
  background-color: #6c757d;
  border-color: #6c757d;
  opacity: 0.65;
}

.btn-secondary {
  background-color: #6c757d;
  border-color: #6c757d;
  color: #fff;
}

.btn-secondary:hover:not(:disabled) {
  background-color: #545b62;
  border-color: #4e555b;
}

.btn-outline-primary {
  color: #043DFF;
  border-color: #043DFF;
}

.btn-outline-primary:hover {
  background-color: #043DFF;
  border-color: #043DFF;
  color: #fff;
}

.btn-outline-danger {
  color: #dc3545;
  border-color: #dc3545;
}

.btn-outline-danger:hover {
  background-color: #dc3545;
  border-color: #dc3545;
  color: #fff;
}

/* Required field indicator */
.text-danger {
  color: #dc3545 !important;
}

/* Grid spacing */
.row.g-3 > * {
  padding: 0.75rem;
}

/* Icon styling */
.fas {
  font-size: 0.875rem;
}

/* File type icons */
.fa-file-pdf {
  color: #dc3545 !important;
}

.fa-file-word {
  color: #043DFF !important;
}

.fa-file-excel {
  color: #28a745 !important;
}

.fa-file-powerpoint {
  color: #ffc107 !important;
}

.fa-file-image {
  color: #17a2b8 !important;
}

.fa-file {
  color: #6c757d !important;
}

/* Close button */
.btn-close {
  background: none;
  border: none;
  font-size: 1.5rem;
  font-weight: 700;
  line-height: 1;
  color: #fff;
  opacity: 0.5;
  transition: opacity 0.15s ease-in-out;
}

.btn-close:hover {
  opacity: 0.75;
}

.btn-close:focus {
  outline: none;
  box-shadow: none;
}

/* Alert styling */
.alert-info {
  background-color: #d1ecf1;
  border-color: #bee5eb;
  color: #0c5460;
}

/* Spinner */
.spinner-border-sm {
  width: 1rem;
  height: 1rem;
}

/* Responsive adjustments */
@media (max-width: 768px) {
  .modal-dialog {
    margin: 0.5rem;
    max-width: calc(100% - 1rem);
  }
  
  .modal-body {
    padding: 1rem;
    max-height: 60vh;
  }
  
  .modal-header, .modal-footer {
    padding: 0.75rem 1rem;
  }
  
  .row.g-3 > * {
    padding: 0.5rem;
  }

  .file-drop-zone {
    padding: 1rem;
    min-height: 150px;
  }

  .file-drop-content h5 {
    font-size: 1rem;
  }
}

/* RTL Support */
[dir="rtl"] .modal-header {
  text-align: right;
}

[dir="rtl"] .me-2 {
  margin-right: 0 !important;
  margin-left: 0.5rem !important;
}

[dir="rtl"] .ms-2 {
  margin-left: 0 !important;
  margin-right: 0.5rem !important;
}

/* Animation for modal */
.modal.fade .modal-dialog {
  transition: transform 0.3s ease-out;
  transform: translate(0, -50px);
}

.modal.show .modal-dialog {
  transform: none;
}

/* Form validation states */
.form-control:valid {
  border-color: #28a745;
}

.form-control:valid:focus {
  border-color: #28a745;
  box-shadow: 0 0 0 0.2rem rgba(40, 167, 69, 0.25);
}

/* Loading state for submit button */
.btn:disabled {
  cursor: not-allowed;
}

/* Custom scrollbar for modal body */
.modal-body::-webkit-scrollbar {
  width: 6px;
}

.modal-body::-webkit-scrollbar-track {
  background: #f1f1f1;
  border-radius: 3px;
}

.modal-body::-webkit-scrollbar-thumb {
  background: #c1c1c1;
  border-radius: 3px;
}

.modal-body::-webkit-scrollbar-thumb:hover {
  background: #a8a8a8;
}

/* Drag and drop animations */
.file-drop-zone.drag-over {
  border-color: #17a2b8;
  background-color: #e6f7ff;
  transform: scale(1.02);
}

/* File preview animations */
.selected-file-display {
  animation: slideIn 0.3s ease-out;
}

@keyframes slideIn {
  from {
    opacity: 0;
    transform: translateY(-10px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
